<template>
  <ul>
    <li>
      <button @click="()=>{setMode('elemental')}"></button>
      <span v-if="currentMode == 'elemental'"></span>
    </li>
    <li>
      <button @click="()=>{setMode('juniorHigh')}"></button>
      <span v-if="currentMode == 'juniorHigh'"></span>
    </li>
    <li>
      <button @click="()=>{setMode('high')}"></button>
      <span v-if="currentMode == 'high'"></span>
    </li>
  </ul>
</template>

<style scoped>
  button {
    font-size: 2rem;
    width:14rem;
    text-align: left;
  }
  ul {
    padding-inline: 0;
    padding-block: 4em;
    list-style: none;
  }
  li {
    margin-block: 0.7rem;
  }
</style>

<script setup>
  import { defineEmits, defineProps, ref, watch } from 'vue';

  const props = defineProps(["config", "configWatcher", "isFirstSetting"]);
  const emit = defineEmits(['onInputedAll', "updateConfig"]);
  
  const currentMode = ref(props.config.mode);
  watch(()=>props.configWatcher, ()=>{
    currentMode.value = props.config.mode;
  })

  const setMode = function(mode){
    if(mode != currentMode.value){
      let modeText = ""
      if(mode == "elemental"){
        modeText = "";
      }
      if(mode == "juniorHigh"){
        modeText = "";
      }
      if(mode == "high"){
        modeText = "";
      }
      const result = confirm(`${modeText}${"\n"} `);
      if(result){
        emit("updateConfig", mode);
        emit("onInputedAll");
      }
    }
  }
</script>